html {
  height: 100%;
  font-family: 'Ubuntu Mono', Consolas, Monaco, 'Andale Mono', monospace;
  font-size: 150%;
  line-height: 1.5;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
}

body {
  min-height: 100%;
  margin: 0;
}

h1, h2, h3, p, ul {
  margin: 0 0 1.5rem 0;
}

h1 {
  font-size: 3em;
  line-height: 1.3333;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: #007acd;
}

a:hover {
  color: #000;
  background-color: #00FDDC;
}

.page-wrapper {
  padding: 1.5rem;
}

.page-header--title {
  font-size: 2.5em;
  line-height: 1.2;
  margin-bottom: 0;
}
